.burger > div {
  width: 25px;
  height: 4px;
  background: #272727;
  border-radius: 4px;
}

.burger > div:before, .burger > div:after {
  content: "";
  width: 25px;
  height: 4px;
  background: #272727;
  border-radius: 4px;
  display: block;
  position: absolute;
}

.burger > div:before {
  margin-top: -10px;
}

.burger > div:after {
  margin-top: 10px;
}

.burger1 {
  transition: all .7s cubic-bezier(.68, -.35, .265, 1.35);
}

.burger1 > div {
  transition: all .1s .3s;
}

.burger1 > div:before, .burger1 > div:after {
  transition: all .3s .2s;
}

.burger1.toggled {
  transform: rotate(180deg);
}

.burger1.toggled > div:before {
  background-color: #ececec;
  transform: rotate(45deg)translate(6px, 8px);
}

.burger1.toggled > div {
  background: none;
}

.burger1.toggled > div:after {
  background-color: #ececec;
  transform: rotate(-45deg)translate(6px, -8px);
}

nav.navrr {
  z-index: 9;
  height: 100%;
  width: 80%;
  background-color: #272727;
  padding: 100px 50px;
  transition: transform .6s cubic-bezier(.785, .135, .15, .86);
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(100%);
}

nav.navrr.show {
  transform: translateX(0);
}

nav.navrr.show ul.main li {
  opacity: 1;
  transform: translateX(0);
}

nav.navrr.show ul.main li:nth-child(1) {
  transition-delay: .2s;
}

nav.navrr.show ul.main li:nth-child(2) {
  transition-delay: .4s;
}

nav.navrr.show ul.main li:nth-child(3) {
  transition-delay: .6s;
}

nav.navrr.show ul.main li:nth-child(4) {
  transition-delay: .8s;
}

nav.navrr.show ul.main li:nth-child(5) {
  transition-delay: 1s;
}

nav.navrr.show ul.main li:nth-child(6) {
  transition-delay: 1.2s;
}

nav.navrr.show ul.main li:nth-child(7) {
  transition-delay: 1.4s;
}

nav.navrr.show ul.main li:nth-child(8) {
  transition-delay: 1.6s;
}

nav.navrr.show ul.main li:nth-child(9) {
  transition-delay: 1.8s;
}

nav.navrr ul.main {
  list-style-type: none;
}

